
.App {
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-template-rows: 50px 29rem;
    text-align: center;
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  .buttonGrid {
    grid-row: 3/4;
    grid-column: 2/3;
    padding-bottom: 100px;
  }
  
  #instructFrame {
    width: 200px;
    object-fit: contain;
    text-align: center;
    top: 0;
    margin-left: 17px;
    grid-row: 1/span 2;
    grid-column: 1/2;
  }
  
  .resultsDiv {
    grid-column: 3/4;
    grid-row: 2/3;
    width: 175px;
    height: 100px;
    background-color: #c7c6c7;
    border: 6px solid black;
    font-weight: bold;
    margin-right: 20px;
    margin-top: 15px;
    justify-self: right;
  }
  
  .targetNumber {
    grid-row: 2/3;
    grid-column: 4/5;
    padding-top: 5px;
    padding-bottom: 5px;
  }
  
  .currentValue {
    border-top: 1px solid black;
    padding-top: 5px;
    padding-bottom: 5px;
  }
  
  .gamesWon {
    border-top: 1px solid black;
    padding-top: 5px;
    padding-bottom: 5px;
  }


  /* ---------------------------- */
  /* desktop */
@media (min-width: 1025px) {
.App {
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 50px 35rem;
  text-align: center;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.buttonGrid {
  grid-row: 3/4;
  grid-column: 2/span 2;
  padding-bottom: 100px;
}

#instructFrame {
  width: 250px;
  object-fit: contain;
  text-align: center;
  margin-left: 20px;
  top: 0;
  grid-row: 1/span 2;
  grid-column: 1/2;
}

.resultsDiv {
  grid-column: 4/5;
  grid-row: 2/3;
  width: 200px;
  height: 100px;
  /* text-align: center; */
  background-color: #c7c6c7;
  border: 6px solid black;
  font-weight: bold;
  /* align-items: center; */
  margin-left: 60px;
  margin-top: 15px;
}

.targetNumber {
  grid-row: 2/3;
  grid-column: 4/5;
  padding-top: 5px;
  padding-bottom: 5px;
}

.currentValue {
  border-top: 1px solid black;
  padding-top: 5px;
  padding-bottom: 5px;
}

.gamesWon {
  border-top: 1px solid black;
  padding-top: 5px;
  padding-bottom: 5px;
}

}
/* end desktop */

