div {
  background-color: transparent;
  outline: none;
  border: none;
  z-index: 100;
}

.zoom {
  -webkit-transition: .2s;
  transition: .2s;
}

.zoom:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

